/**
 * üì≤ Ê≤àÁøîÂ≠¶Ê†° - PWA ÂÆâË£ÖÊèêÁ§∫ÁªÑ‰ª∂ (Install Prompt)
 * 
 * ÊèêÁ§∫Áî®Êà∑Â∞ÜÂ∫îÁî®ÂÆâË£ÖÂà∞‰∏ªÂ±èÂπï„ÄÇ
 */

"use client"

import { useState, useEffect } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { Download, X, Smartphone, Share, Plus } from "lucide-react"
import { cn } from "@/lib/utils"
import { brandColors } from "@/lib/design-tokens"

// ============================================
// Á±ªÂûãÂÆö‰πâ
// ============================================

interface BeforeInstallPromptEvent extends Event {
  prompt: () => Promise<void>
  userChoice: Promise<{ outcome: "accepted" | "dismissed" }>
}

// ============================================
// Ê£ÄÊµãËÆæÂ§áÁ±ªÂûã
// ============================================

function getDeviceInfo() {
  if (typeof window === "undefined") {
    return { isIOS: false, isAndroid: false, isStandalone: false }
  }

  const ua = navigator.userAgent
  const isIOS = /iPad|iPhone|iPod/.test(ua) && !(window as any).MSStream
  const isAndroid = /Android/.test(ua)
  const isStandalone = 
    window.matchMedia("(display-mode: standalone)").matches ||
    (window.navigator as any).standalone === true

  return { isIOS, isAndroid, isStandalone }
}

// ============================================
// ÂÆâË£ÖÊèêÁ§∫ÁªÑ‰ª∂
// ============================================

export function InstallPrompt() {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null)
  const [showPrompt, setShowPrompt] = useState(false)
  const [showIOSGuide, setShowIOSGuide] = useState(false)
  const [dismissed, setDismissed] = useState(false)

  useEffect(() => {
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÂÆâË£ÖÊàñÂ∑≤ÁªèÂÖ≥Èó≠Ëøá
    const hasInstalled = localStorage.getItem("pwa-installed")
    const hasDismissed = localStorage.getItem("pwa-dismissed")
    
    if (hasInstalled || hasDismissed) {
      return
    }

    const { isIOS, isStandalone } = getDeviceInfo()

    // Â∑≤ÁªèÊòØ standalone Ê®°ÂºèÔºå‰∏çÊòæÁ§∫
    if (isStandalone) {
      localStorage.setItem("pwa-installed", "true")
      return
    }

    // Android/Chrome ÁöÑÂÆâË£ÖÊèêÁ§∫
    const handleBeforeInstall = (e: Event) => {
      e.preventDefault()
      setDeferredPrompt(e as BeforeInstallPromptEvent)
      
      // Âª∂ËøüÊòæÁ§∫ÔºåÈÅøÂÖçÊâìÊâ∞Áî®Êà∑
      setTimeout(() => {
        setShowPrompt(true)
      }, 5000)
    }

    window.addEventListener("beforeinstallprompt", handleBeforeInstall)

    // iOS ËÆæÂ§áÊòæÁ§∫ÊâãÂä®ÂÆâË£ÖÊåáÂçó
    if (isIOS) {
      setTimeout(() => {
        setShowIOSGuide(true)
      }, 10000)
    }

    return () => {
      window.removeEventListener("beforeinstallprompt", handleBeforeInstall)
    }
  }, [])

  // Â§ÑÁêÜÂÆâË£Ö
  const handleInstall = async () => {
    if (!deferredPrompt) return

    try {
      await deferredPrompt.prompt()
      const { outcome } = await deferredPrompt.userChoice
      
      if (outcome === "accepted") {
        localStorage.setItem("pwa-installed", "true")
        setDeferredPrompt(null)
      }
    } catch (error) {
      console.error("ÂÆâË£ÖÂ§±Ë¥•:", error)
    }
    
    setShowPrompt(false)
  }

  // ÂÖ≥Èó≠ÊèêÁ§∫
  const handleDismiss = () => {
    setShowPrompt(false)
    setShowIOSGuide(false)
    setDismissed(true)
    localStorage.setItem("pwa-dismissed", Date.now().toString())
  }

  // Á®çÂêéÊèêÈÜí
  const handleLater = () => {
    setShowPrompt(false)
    setShowIOSGuide(false)
    // 24Â∞èÊó∂ÂêéÂÜçÊ¨°ÊèêÈÜí
    localStorage.setItem("pwa-dismissed", (Date.now() - 23 * 60 * 60 * 1000).toString())
  }

  if (dismissed) return null

  return (
    <>
      {/* Android/Chrome ÂÆâË£ÖÊèêÁ§∫ */}
      <AnimatePresence>
        {showPrompt && deferredPrompt && (
          <motion.div
            initial={{ opacity: 0, y: 50, scale: 0.95 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 50, scale: 0.95 }}
            transition={{ type: "spring", damping: 25, stiffness: 300 }}
            className="fixed bottom-20 left-4 right-4 md:left-auto md:right-4 md:w-80 bg-white rounded-2xl shadow-xl border border-slate-100 p-4 z-50"
          >
            <div className="flex items-start gap-3">
              <div 
                className="w-10 h-10 rounded-xl flex items-center justify-center shrink-0"
                style={{ backgroundColor: `${brandColors[50]}` }}
              >
                <Download className="w-5 h-5" style={{ color: brandColors[700] }} />
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="font-semibold text-slate-800 text-sm">
                  ÂÆâË£ÖÊ≤àÁøîÂ≠¶Ê†°
                </h3>
                <p className="text-xs text-slate-500 mt-1">
                  Ê∑ªÂä†Âà∞‰∏ªÂ±èÂπïÔºåËé∑ÂæóÊõ¥Â•ΩÁöÑ‰ΩìÈ™å
                </p>
                <div className="flex gap-2 mt-3">
                  <button
                    onClick={handleInstall}
                    className="px-3 py-1.5 text-xs font-medium text-white rounded-lg hover:opacity-90 transition-colors"
                    style={{ backgroundColor: brandColors[900] }}
                  >
                    ÂÆâË£Ö
                  </button>
                  <button
                    onClick={handleLater}
                    className="px-3 py-1.5 text-xs font-medium text-slate-600 bg-slate-100 rounded-lg hover:bg-slate-200 transition-colors"
                  >
                    Á®çÂêéÂÜçËØ¥
                  </button>
                </div>
              </div>
              <button
                onClick={handleDismiss}
                className="text-slate-400 hover:text-slate-600 p-1 -mr-1 -mt-1"
              >
                <X className="w-4 h-4" />
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* iOS ÂÆâË£ÖÊåáÂçó */}
      <AnimatePresence>
        {showIOSGuide && (
          <motion.div
            initial={{ opacity: 0, y: 50, scale: 0.95 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 50, scale: 0.95 }}
            transition={{ type: "spring", damping: 25, stiffness: 300 }}
            className="fixed bottom-20 left-4 right-4 md:left-auto md:right-4 md:w-80 bg-white rounded-2xl shadow-xl border border-slate-100 p-4 z-50"
          >
            <div className="flex items-start gap-3">
              <div 
                className="w-10 h-10 rounded-xl flex items-center justify-center shrink-0"
                style={{ backgroundColor: `${brandColors[50]}` }}
              >
                <Smartphone className="w-5 h-5" style={{ color: brandColors[700] }} />
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="font-semibold text-slate-800 text-sm">
                  Ê∑ªÂä†Âà∞‰∏ªÂ±èÂπï
                </h3>
                <div className="mt-2 space-y-2">
                  <div className="flex items-center gap-2 text-xs text-slate-600">
                    <span className="w-5 h-5 rounded bg-slate-100 flex items-center justify-center text-[10px] font-medium">1</span>
                    <span>ÁÇπÂáªÂ∫ïÈÉ®</span>
                    <Share className="w-4 h-4 text-blue-500" />
                    <span>ÂàÜ‰∫´ÊåâÈíÆ</span>
                  </div>
                  <div className="flex items-center gap-2 text-xs text-slate-600">
                    <span className="w-5 h-5 rounded bg-slate-100 flex items-center justify-center text-[10px] font-medium">2</span>
                    <span>ÈÄâÊã©</span>
                    <Plus className="w-4 h-4 text-slate-500" />
                    <span>Ê∑ªÂä†Âà∞‰∏ªÂ±èÂπï</span>
                  </div>
                </div>
                <button
                  onClick={handleLater}
                  className="mt-3 px-3 py-1.5 text-xs font-medium text-slate-600 bg-slate-100 rounded-lg hover:bg-slate-200 transition-colors"
                >
                  Áü•ÈÅì‰∫Ü
                </button>
              </div>
              <button
                onClick={handleDismiss}
                className="text-slate-400 hover:text-slate-600 p-1 -mr-1 -mt-1"
              >
                <X className="w-4 h-4" />
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  )
}

// ============================================
// ÈªòËÆ§ÂØºÂá∫
// ============================================

export default InstallPrompt
